* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

html, body, #root {
	height: 100%;
}

body {
  margin: 0;
	padding: 0;
	font-family: Arial, sans-serif;
	font-size: 16px;
	color: #333;
}

svg {
	overflow: visible !important;
}

button {
	font-size: 16px;
	margin-right: 10px;
	padding: 10px;
	border-radius: 3px;
	background: transparent;
	outline: none;
	border: 1px solid #333;
	cursor: pointer;
	color: #333;
}

button:hover {
	border-color: #aaa;
}

input[type='range'] {
	margin-right: 10px;
	background: #666;
	height: 10px;
}

input[type='range']::-webkit-slider-thumb{
	background: #ccc;
	height: 30px;
	width: 30px;
	border-radius: 30px;
}

.maze-container {
	padding: 20px 0;
	text-align: center;
}

#maze {
	overflow: visible !important;
	margin-bottom: 40px;
}


